'use client';

import { createContext, useState, useEffect, useCallback } from 'react';
import { END_POINT } from '@/config';

export const AuthContext = createContext();

export function AuthProvider({ children }) {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [userId, setUserId] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const res = await fetch(END_POINT.VERIFY_TOKEN, {
          method: 'GET',
          credentials: 'include',
          headers: {
            'Content-Type': 'application/json',
          },
        });

        const json = await res.json();
        
        if (json.successful && json.data) {
          setIsLoggedIn(true);
          setUserId(json.data);
        } else {
          setIsLoggedIn(false);
          setUserId(null);
        }
      } catch (error) {
        console.error('Auth check failed:', error.message);
        setIsLoggedIn(false);
        setUserId(null);
      } finally {
        setLoading(false);
      }
    };

    checkAuth();
  }, []);

  // Logout function
  const logout = useCallback(async () => {
    try {
      await fetch(END_POINT.LOGOUT, {
        method: 'GET',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      setIsLoggedIn(false);
      setUserId(null);
      window.location.reload();
    } catch (error) {
      console.error('Logout failed:', error.message);
    }
  }, []);

  // Login function (to be called after successful login)
  const login = useCallback((newUserId) => {
    setIsLoggedIn(true);
    setUserId(newUserId);
  }, []);

  return (
    <AuthContext.Provider value={{ isLoggedIn, userId, loading, logout, login }}>
      {children}
    </AuthContext.Provider>
  );
}
